(function () {

    var element;
    // 指定图表的配置项和数据
    option = {
        legend: {
            data: [{name: '销售额(千万元)', icon: "rect"}, {name: '销售数量(家)', icon: "circle"}],
            top: "10",
            left: "33",
            textStyle: {
                color: "#fff",
                fontSize: 14,
                fontFamily: '微软雅黑'
            },
            itemHeight: 12,
            itemWidth: 12,
            itemGap: 50
        },
        tooltip: {
            trigger: "axis",
            formatter: "{b0}<br/>销售额 : {c0}万<br />销售数量 : {c1}万",
            axisPointer: {
                type: "shadow"
            }
        },
        xAxis: [
            {
                type: 'category',
                axisLabel: {
                    textStyle: {
                        color: "#9c9bad",
                        fontSize: 12,
                        fontFamily: '微软雅黑'
                    },
                    margin: 12,
                    interval: 0
                },
                axisTick: {
                    length: 8,
                    lineStyle: {
                        color: "#404E79",
                        width: 2
                    }
                },
                splitLine: {
                    show: false
                },
                axisLine: {
                    lineStyle: {
                        color: "#404E79"
                    }
                }
            }
        ],
        yAxis: [
            {
                type: 'value',
                min: 0,
                max: 6,
                interval: 1,
                axisLabel: {
                    formatter: function (v) {
                        return v + 'W\n'
                    },
                    margin: 10,
                    textStyle: {
                        fontSize: 12,
                        fontFamily: '微软雅黑',
                        color: "#9c9aac"
                    }
                },
                axisTick: {
                    show: false
                },
                axisLine: {
                    show: false
                },
                splitLine: {
                    lineStyle: {
                        color: '#4d5e91', //分割线颜色
                        type: "dashed"
                    }
                }
            },
            {
                type: 'value',
                min: 0,
                max: 6,
                interval: 1,
                axisLabel: {
                    formatter: function (v) {
                        return v + 'W\n'
                    },
                    textStyle: {
                        color: "#9c9aac",
                        fontSize: 12,
                        fontFamily: '微软雅黑'
                    }
                },
                axisTick: {
                    show: false
                },
                axisLine: {
                    show: false
                },
                splitLine: {
                    lineStyle: {
                        color: '#4d5e91', //分割线颜色
                        type: "dashed"
                    }
                }
            }
        ],
        color: ["#3b7ef4", "#3dfdb7"],
        series: [
            {
                name: '销售额(千万元)',
                type: 'bar',
                itemStyle: {
                    normal: {
                        color: "#3BA2F9"
                    }
                },
                barCategoryGap: "60%"
            },
            {
                name: '销售数量(家)',
                type: 'line',
                yAxisIndex: 1,
                smooth: true,
                symbol: 'circle',
                symbolSize: 12,
                itemStyle: {
                    normal: {
                        borderColor: "#1f2b65",
                        borderWidth: 2
                    }
                },
                lineStyle: {
                    normal: {
                        width: 3
                    }
                }
            }
        ]
    };

    var main = function (/* param */ p) {

        // 初始化
        _render_init_(p);

        // 执行渲染
        _render_set_(p);

    };

    var _render_init_ = function (p) {
        // 指定图表的配置项和数据

        var e = element = document.getElementById(p.id);

        e.style.width = p.width + 'px';
        e.style.height = p.height + 'px';

    };

    var _render_set_ = function (p) {

        var myChart = echarts.init(element);
        option.series[0].data = p.data[0];
        option.xAxis[0].data = p.categories;
        option.series[1].data = p.data[1];


        // 使用刚指定的配置项和数据显示图表。
        myChart.setOption(option);

    };

    // 接口
    if (!window.render) {

        window.render = function (v) {

            window.render._index_(v);

        };

    }

    window.render._index_ = window.render['8d998cf5-8cc4-4ac9-a2d9-d8fe7051ed40'] = main;

}());
