$('.checkMoreBox').delegate('.checkMore','click',function(){
	$('.popOut').show(300);
});
$('.visitorName').click(function(){
	$('.popOut').show(300);
});
$('.closeBtnBox').delegate('.closeBtn','click',function(){
	$('.popOut').hide(300);
	
});


$('.radioBtn').parent().click(function(){
	$(this).children('span').addClass('bgactive');
	$(this).siblings().children('span').removeClass('bgactive')
});


$('.navList').children('span').click(function(){
	$(this).addClass('active').siblings().removeClass('active');
	var index=$(this).index();
	$('.listContent').eq(index).addClass('show').siblings('.listContent').removeClass('show');
});    