exports.createInfo=function(id, width, height, dataset, clickFn) {
    var publicGraph = {};
    publicGraph.percent = {
        male: (dataset.male / (dataset.male + dataset.female) * 100).toFixed(2),
        female: (dataset.female / (dataset.male + dataset.female) * 100).toFixed(2)
    };
    publicGraph.iconHeight = 50;
    publicGraph.maxLen = 13;

    publicGraph.male = Math.floor(dataset.male / 1000);
    if(dataset.male % 1000 > 0) {
        publicGraph.male++;
    }
    publicGraph.maleLength = Math.floor(publicGraph.male / publicGraph.maxLen);
    if(publicGraph.male % publicGraph.maxLen > 0) {
        publicGraph.maleLength++;
    }
    publicGraph.female = Math.floor(dataset.female / 1000);
    if(dataset.female % 1000 > 0) {
        publicGraph.female++;
    }
    publicGraph.femaleLength = Math.floor(publicGraph.female / publicGraph.maxLen);
    if(publicGraph.female % publicGraph.maxLen > 0) {
        publicGraph.femaleLength++;
    }

    //------------------------------function end--------------------------
    function setMaleImage(len, index) {
        var data = [];
        for(var i = 0; i < len; i++) {
            data.push(i);
        }
        svg.selectAll("svg")
            .data(data)
            .enter()
            .append("g")
            .attr("transform", function(d, i) {
                return "translate(" + (padding.left + xScale(i)) + "," + (padding.top + index * publicGraph.iconHeight) + ")";
            }).append("image")
            .attr("width", 17)
            .attr("height", 38)
            .attr("xlink:href", function(d) {
                return "../img/男性.png";
            })
    }

    function setFemaleImage(len, index) {
        var data = [];
        for(var i = 0; i < len; i++) {
            data.push(i);
        }
        svg.selectAll("svg")
            .data(data)
            .enter()
            .append("g")
            .attr("transform", function(d, i) {
                return "translate(" + (padding.left + xScale(i)) + "," + (padding.top + 15 + index * publicGraph.iconHeight) + ")";
            }).append("image")
            .attr("width", 17)
            .attr("height", 38)
            .attr("xlink:href", function(d) {
                return "../img/女性.png";
            })
    }
    //------------------------------function end--------------------------

    //画布周边的空白
    var padding = {
        left: 100,
        right: 30,
        top: 10,
        bottom: 10
    };

    var _width = width - padding.left - padding.right;
    var _height = height - padding.top - padding.bottom;

    //绘制一个svg
    var svg = d3.select("#" + id).append("svg")
        .attr("width", width)
        .attr("height", height);

    var data = [];
    for(var i = 0; i < publicGraph.maxLen; i++) {
        data.push(i);
    }

    //x轴的比例尺
    var xScale = d3.scale.ordinal()
        .domain(data)
        .rangeRoundBands([0, _width]);
    var index = 0;
    var textGroupMale = svg.append("g")
        .attr("transform", "translate(50," + padding.top + ")");
    textGroupMale.append("text")
        .attr("dy", 20)
        .attr("font-size", "14px")
        .attr("font-family", "微软雅黑")
        .attr("fill", "#FFF")
        .style("text-anchor", function(d) {
            return "middle";
        })
        .text("男性占比");
    textGroupMale.append("text")
        .attr("dy", 50)
        .attr("font-size", "24px")
        .attr("font-family", "微软雅黑")
        .attr("fill", "#3b7ef4")
        .style("text-anchor", function(d) {
            return "middle";
        })
        .text(publicGraph.percent.male + "%");
    for(var i = 0; i < publicGraph.maleLength; i++) {
        var len = publicGraph.maxLen;
        if(i == publicGraph.maleLength - 1) {
            len = publicGraph.male % publicGraph.maxLen;
            if(len == 0) len = publicGraph.maxLen;
        }
        setMaleImage(len, index);
        index++;
    }
    var textGroupFemale = svg.append("g")
        .attr("transform", "translate(50," + (padding.top + 15 + index * publicGraph.iconHeight) + ")");
    textGroupFemale.append("text")
        .attr("dy", 20)
        .attr("font-size", "14px")
        .attr("font-family", "微软雅黑")
        .attr("fill", "#FFF")
        .style("text-anchor", function(d) {
            return "middle";
        })
        .text("女性占比");
    textGroupFemale.append("text")
        .attr("dy", 50)
        .attr("font-size", "24px")
        .attr("font-family", "微软雅黑")
        .attr("fill", "#3b7ef4")
        .style("text-anchor", function(d) {
            return "middle";
        })
        .text(publicGraph.percent.female + "%");
    for(var i = 0; i < publicGraph.femaleLength; i++) {
        var len = publicGraph.maxLen;
        if(i == publicGraph.femaleLength - 1) {
            len = publicGraph.female % publicGraph.maxLen;
            if(len == 0) len = publicGraph.maxLen;
        }
        setFemaleImage(len, index);
        index++;
    }

    return publicGraph;
}