(function () {

    var element;
    // 指定图表的配置项和数据
    var option = {
        title: {
            text: '义乌市电商指数分析',
            padding: [20, 20],
            textStyle: {
                fontSize: 18,
                fontWeight: "lighter",
                color: "#fff"
            },
            show:false
        },
        color: ['#f0a629', '#f54fc5', '#784ff2', '#3a7df4', '#2bfeb5'],
        series: [
            {
                name: '义乌市电商指数分析',
                type: 'pie',
                radius: ['15%', '70%'],
                center: ['50%', '50%'],
                itemStyle: {},
                roseType: "area",
                label: {
                    normal: {
                        position: 'outside',
                        show: true,
                        formatter: "{d}%\n\n{b}",
                        textStyle: {
                            color: "#fff",
                            fontSize: 12,
                            fontFamily: "微软雅黑"
                        }
                    }
                },
                labelLine: {
                    normal: {
                        lineStyle: {
                            color: "#fff"
                        }
                    }
                }
            }
        ]
    };

    var main = function (/* param */ p) {

        // 初始化
        _render_init_(p);

        // 执行渲染
        _render_set_(p);

    };

    var _render_init_ = function (p) {


        var e = element = document.getElementById(p.id);

        e.style.width = p.width + 'px';
        e.style.height = p.height + 'px';

    };

    var _render_set_ = function (p) {

        var myChart = echarts.init(element);

        option.series[0].data = p.data;

        // 使用刚指定的配置项和数据显示图表。
        myChart.setOption(option);

    };

    // 接口
    if (!window.render) {

        window.render = function (v) {

            window.render._index_(v);

        };

    }

    window.render._index_ = window.render['de2e185e-4c7e-4a59-88d7-b4a95a063196'] = main;

}());
