//行业趋势
exports.industryTrends = function(id) {
    require("./double_line.js");
    var width = $("#" + id).width();
    var height = $("#" + id).parent().height() - 30;
    render['fa9e8f04-ea91-45be-b4de-b1e0f8aceab7']({
        id: id,
        width: width,
        height: height,
        data : [
			[2500, 4000, 3800, 4500, 3800, 4500],
			[4000, 4500, 5000, 5500, 5700, 5800]
		],
			categories:['2015年1月','2015年2月','2015年3月','2015年4月','2015年5月',
				'2015年6月']
    });
}
//行业规模
exports.hygm = function(id) {
    require("./zhu_line_small.js");
    var width = $("#" + id).width();
    var height = $("#" + id).height();
    render['8d998cf5-8cc4-4ac9-a2d9-d8fe7051ed40']({
        id: id,
        width: width,
        height: height,
        data: [
            [5, 3, 4.5, 6, 5.5],
            [3, 4, 5, 4, 4]
        ],
        categories: ['2016年1月', '2016年2月', '2016年3月', '2016年4月', '2016年5月']
    });
}	
//Tab切换
$(".Tab").delegate("li","click",function(){
	
		var index = $(this).index();
		$(this).addClass("active").siblings().removeClass("active");
		$(".ElecSupTransReportBox .TabCon").eq(index).addClass("active").siblings().removeClass("active");
		if(index==3){
			exports.hygm('hygm')
		}
})
//切换按钮切换
$(".industryTrendChart").delegate(".change","click",function(){

	if($(".industryTrendsBox").hasClass("active")){
		$(".industryTrendsBox").removeClass("active");
		$(".industryTrendsTab").addClass("active");
	}else{
		$(".industryTrendsBox").addClass("active");
		$(".industryTrendsTab").removeClass("active");
	}
})
//单选按钮切换
$('.radioBtn').parent().click(function(){
	$(this).children('span').addClass('bgactive');
	$(this).siblings().children('span').removeClass('bgactive')
});
                              
