var aBtnBox=document.getElementById('leftNavList');
var aBtn=aBtnBox.getElementsByTagName('li');
var aDivBox=document.getElementById('posAbsRightBox');
var aDiv=aDivBox.getElementsByTagName('div');
	for (var i=0; i<aBtn.length; i++)
	{
		(function (){
			var timer=null;
			aBtn[i].index=i;
			aBtn[i].onmouseover=function (){
				clearTimeout(timer);
				timer=setTimeout(function (){
					aDiv[this.index].show();					
				}, 200);
			};
			
			aBtn[i].onmouseout=function (){
				clearTimeout(timer);
				timer=setTimeout(function (){
					aDiv[this.index].hide();
				}, 200);
			};
		})();
	}
