exports.right_btm = function(id) {
    function createRandomString(n) {
        var elements = "abcdefghijklmnopqrstuvwxyz-".split("");
        var string = "";
        for (var i = 0; i < n; i++) {
            var e = elements[Math.floor(Math.random() * elements.length)];
            string += i == 0 ? e.toUpperCase() : e;
        }
        return string;
    }
    // 创建随机数据.
    var categories = [{
        name: "发生",
        color: "#3b7ef4"
    }, {
        name: "报道",
        color: "#734be9"
    }, {
        name: "平息",
        color: "#35d1ab"
    }, {
        name: "应对",
        color: "#f54fc5"
    }];

    function createRandomData() {
        var data = [];

        var numItems = Math.round(10 + Math.random() * 10);
        for (var i = 0; i < numItems; i++) {
            data.push({
                name: createRandomString(2 + Math.random() * 5),
                size: 1 + Math.random() * 50,
                group: categories[Math.floor(Math.random() * 4)].name
            });
        }
        return data;
    }

    // var width = 300;
    // var height = 200;
    // $("#" + id).parent().css('padding-top',"20px")
    var width = $("#" + id).width();
    var height = $("#" + id).parent().height();
    var data = createRandomData();

    // 演示开始
    var chart = new BubbleCloudChart(document.getElementById(id), width, height);
    chart.categories = categories;

    chart.minRadius = 4;
    chart.maxRadius = 10;
    chart.collisionPadding = 10;
    chart.minCollisionRadius = 15;
    chart.labelFunction = null;
    chart.iconPadding = 70;


    chart.init();
    chart.data(data);
}