exports.setBarCharts = function(id, data, categories) {
    var myChart = echarts.init(document.getElementById(id));
    var pdata = [],
        sum0 = 0,
        sum1 = 0,
        sum2 = 0,
        sum3 = 0,
        sum4 = 0,
        sum5 = 0,
        sum6 = 0;
    for (var k = 0; k < data[0].length; k++) {
        for (var l = 0; l < data[0][k].length; l++) {
            switch (k) {
                case 0:
                    sum0 += data[0][k][l];
                    break;
                case 1:
                    sum1 += data[0][k][l];
                    break;
                case 2:
                    sum2 += data[0][k][l];
                    break;
                case 3:
                    sum3 += data[0][k][l];
                    break;
                case 4:
                    sum4 += data[0][k][l];
                    break;
                case 5:
                    sum5 += data[0][k][l];
                    break;
                case 6:
                    sum6 += data[0][k][l];
                    break;
            }
        }
    }
    pdata.push({
        value: sum0,
        name: "服装鞋包"
    });
    pdata.push({
        value: sum1,
        name: "母婴用品"
    });
    pdata.push({
        value: sum2,
        name: "家居用品"
    });
    pdata.push({
        value: sum3,
        name: "数码"
    });
    pdata.push({
        value: sum4,
        name: "运动户外"
    });
    pdata.push({
        value: sum5,
        name: "保健"
    });
    var option = {
        grid: {
            top: 100
        },
        tooltip: {
            show: true,
            showContent: true,
            backgroundColor: "rgba(50,50,50,0)",
            position: function(point, params, dom) {
                return [point[0] - 15, '12%'];
            },
            formatter: function(params, ticket, callback) {
                if (params.componentSubType == "bar") {
                    return params.data;
                }
            }
        },
        legend: {
            top: '30',
            left: '20',
            data: [{
                name: "服装鞋包",
                icon: "rect",
                textStyle: {
                    color: "#FFF",
                    fontSize: "14",
                    fontFamily: "微软雅黑"
                }
            }, {
                name: "母婴用品",
                icon: "rect",
                textStyle: {
                    color: "#FFF",
                    fontSize: "14",
                    fontFamily: "微软雅黑"
                }
            }, {
                name: "家居用品",
                icon: "rect",
                textStyle: {
                    color: "#FFF",
                    fontSize: "14",
                    fontFamily: "微软雅黑"
                }
            }, {
                name: "数码",
                icon: "rect",
                textStyle: {
                    color: "#FFF",
                    fontSize: "14",
                    fontFamily: "微软雅黑"
                }
            }, {
                name: "运动户外",
                icon: "rect",
                textStyle: {
                    color: "#FFF",
                    fontSize: "14",
                    fontFamily: "微软雅黑"
                }
            }, {
                name: "保健",
                icon: "rect",
                    textStyle: {
                        color: "#FFF",
                        fontSize: "14",
                        fontFamily: "微软雅黑"
                    }
                }
            ],
            itemWidth: 16,
            itemHeight: 16,
            itemGap: 35
        },
        color: ["#2bfeb5", "#3a7df4", "#784ff2", "#f54fc5", "#112fc5", "#c51fc1"],
        xAxis: [{
            type: 'category',
            data: categories,
            splitLine: {
                show: false
            },
            axisLabel: {
                interval: 0,
                textStyle: {
                    color: '#fff',
                    fontSize: '14',
                    fontFamily: "微软雅黑"
                }
            }
        }],
        yAxis: [{
            type: 'value',
            name: '',
            nameTextStyle: {
                color: "#8b9cd2",
                fontSize: '14',
                fontFamily: "微软雅黑"
            },
            axisLabel: {
                textStyle: {
                    color: '#8b9cd2',
                    fontSize: '14',
                    fontFamily: "微软雅黑"
                },
                formatter: '{value}'
            },
            axisLine: {
                show: false
            },
            position: "left",
            splitLine: {
                lineStyle: {
                    color: '#455288',
                    type: 'dashed',
                    width: '1'
                }
            }
        }],
        series: [{
            name: '服装鞋包',
            type: 'bar',
            data: data[0][0],
            barCategoryGap: "50%"
        }, {
            name: '母婴用品',
            type: 'bar',
            data: data[0][1]
        }, {
            name: '家居用品',
            type: 'bar',
            data: data[0][2]
        }, {
            name: '数码',
            type: 'bar',
            data: data[0][3]
        }, {
            name: '运动户外',
            type: 'bar',
            data: data[0][4]
        }, {
            name: '保健',
            type: 'bar',
            data: data[0][5]
        }, {
            name: '访问来源',
            type: 'pie',
            radius: '20%',
            center: ['80%', '35%'],
            label: {
                normal: {
                    position: 'outside',
                    show: false,
                    formatter: function(a){
                        return a.percent+"%" +"\n\n"+a.value+"(千万元)"
                    },
                    // formatter: "{d}%\n\n{b}",
                    textStyle: {
                        color: "#fff",
                        fontSize: 12,
                        fontFamily: "微软雅黑"
                    }
                },
                emphasis: {
                    show: true
                }
            },
            labelLine: {
                normal: {
                    length: 30,
                    length2: 20,
                    lineStyle: {
                        color: "#fff"
                    }
                },
                emphasis: {
                    lineStyle: {}
                }
            },
            itemStyle: {
                normal: {
                    borderWidth: 1,
                    borderColor: "#1a1254"
                }
            },
            data: pdata
        }]
    };
    myChart.setOption(option);
    var j = 1;
    var aa=setInterval(function() {
        var dater = [];
        for (var i = 0; i < 4; i++) {
            dater.push({
                data: data[j][i]
            });
        }
        pdata = [];
        sum0 = 0;
        sum1 = 0;
        sum2 = 0;
        sum3 = 0;
        sum4 = 0;
        sum5 = 0;
        for (var k = 0; k < data[j].length; k++) {
            for (var l = 0; l < data[j][k].length; l++) {
                switch (k) {
                    case 0:
                        sum0 += data[j][k][l];
                        break;
                    case 1:
                        sum1 += data[j][k][l];
                        break;
                    case 2:
                        sum2 += data[j][k][l];
                        break;
                    case 3:
                        sum3 += data[j][k][l];
                        break;
                    case 4:
                        sum5 += data[j][k][l];
                        break;
                    case 5:
                        sum5 += data[j][k][l];
                        break;
                }
            }
        }
        pdata.push({
            value: sum0,
            name: "服装鞋包"
        });
        pdata.push({
            value: sum1,
            name: "母婴用品"
        });
        pdata.push({
            value: sum2,
            name: "家居用品"
        });
        pdata.push({
            value: sum3,
            name: "数码"
        });
        pdata.push({
            value: sum4,
            name: "运动户外"
        });
        pdata.push({
            value: sum5,
            name: "保健"
        });
        $(".year_ul>li").removeClass();
        $(".year_ul>li").eq(j).addClass("focus_li");
        j++;
        if (j > data.length - 1) j = 0;
        myChart.setOption({
            series: dater
        });
    }, 2000);
}