(function () {

    var element;
    // 指定图表的配置项和数据
    var option = {
        legend: {
            itemWidth: 14,
            itemHeight: 14,
            itemGap: 30,
            data: [
                {
                    name: "税务登记实体数",
                    icon: "rect",
                    textStyle: {
                        color: "#fff",
                        fontSize: 14,
                        fontFamily: "微软雅黑"
                    }
                },
                {
                    name: "实际电商实体数",
                    icon: "rect",
                    textStyle: {
                        color: "#fff",
                        fontSize: 14,
                        fontFamily: "微软雅黑"
                    }
                }
            ]

        },
        tooltip: {
            show: true,
            trigger: 'axis',
            axisPointer: {
                type: 'shadow'
            },
            padding: 15,
            borderWidth: 1,
            borderColor: "#22998c",
            backgroundColor: "rgba(19,12,79,0.9)",
            formatter: function (a) {
                var percent = parseInt(a[0].data / a[1].data * 100);
                return a[0].name + " :<br/>" + a[0].seriesName + " : " + a[0].data + "家<br/>" + a[1].seriesName + " : "
                    + a[1].data + "家<br/>匹配度 : " + percent + "%";
            }
        },
        yAxis: [
            {
                type: 'category',
                minInterval: 10,
                axisTick: {
                    length: 0
                },
                axisLabel: {
                    interval: 0,
                    margin: 20,
                    textStyle: {
                        color: "#fff",
                        fontSize: 14,
                        fontFamily: "微软雅黑"
                    },
                    formatter: function (v) {
                        return v + '\n';
                    }
                },
                splitLine: {
                    show: false
                },
                axisLine: {
                    show: false,
                    lineStyle: {
                        color: "#404E79"
                    }
                }
            }
        ],

        xAxis: [
            {
                type: 'value',
                nameTextStyle: {
                    color: '#fff'
                },
                nameGap: 25,
                axisLabel: {
                    margin: 0,
                    textStyle: {
                        fontSize: 14,
                        color: "#8b9dd2"
                    }

                },
                minInterval: 100,
                axisTick: {
                    show: true,
                    length: 5,
                    inside: true,
                    lineStyle: {
                        color: "#58699b"
                    }
                },
                axisLine: {
                    show: true,
                    lineStyle: {
                        color: "#58699b"
                    }
                },
                splitLine: {
                    show: false
                }
            }
        ],
        series: [
            {
                name: '税务登记实体数',
                type: 'bar',
                itemStyle: {
                    normal: {
                        color: "#2bfeb5"
                    }
                },
                barCategoryGap: "30%"
            },
            {
                name: '实际电商实体数',
                type: 'bar',
                itemStyle: {
                    normal: {
                        color: "#3b7ef4"
                    }
                }
            }
        ]
    };

    var main = function (/* param */ p) {

        // 初始化
        _render_init_(p);

        // 执行渲染
        _render_set_(p);

    };

    var _render_init_ = function (p) {
        // 指定图表的配置项和数据

        var e = element = document.getElementById(p.id);

        e.style.width = p.width + 'px';
        e.style.height = p.height + 'px';

    };

    var _render_set_ = function (p) {

        var myChart = echarts.init(element);

        option.series[0].data = p.data[0];
        option.series[1].data = p.data[1];
        option.yAxis[0].data = p.categories;

        // 使用刚指定的配置项和数据显示图表。
        myChart.setOption(option);

    };

    // 接口
    if (!window.render) {

        window.render = function (v) {

            window.render._index_(v);

        };

    }

    window.render._index_ = window.render['61e2960f-560c-4039-97b0-6e1c5e73cff5'] = main;

}());
