(function () {

    var element;
    // 指定图表的配置项和数据
    var dataStyle = {
        normal: {
            label: {show: false},
            labelLine: {show: false}
        }
    };
    var placeHolderStyle = {
        normal: {
            color: 'rgba(0,0,0,0)',
            label: {show: false},
            labelLine: {show: false}
        },
        emphasis: {
            color: 'rgba(0,0,0,0)'
        }
    };
    var option = {
        tooltip: {
            show: true,
            formatter: function (d) {
                if (d.data.name != "invisible") {
                    return d.name + " : " + d.percent + "%";
                }
            }
        },
        legend: {
            top: "8%",
            left: "53%",
            orient: 'vertical',
            itemGap: 10,
            data: [{name: '15岁-35岁', icon: "image://../img/hcon1.png"},
                {name: '35岁以上', icon: "image://../img/hcon2.png"},
                {name: '15岁以下', icon: "image://../img/hcon3.png"}],
            textStyle: {
                color: "#fff",
                fontSize: 12,
                fontFamily: "微软雅黑"
            }
        },
        color: ["#2bfeb5", "rgba(0,0,0,0)", "#3a7df4", "rgba(0,0,0,0)", "#f54fc5"],
        series: [
            {
                name: '1',
                type: 'pie',
                clockWise: false,
                hoverAnimation:false,
                radius: ["60%", "75%"],
                itemStyle: dataStyle
            },
            {
                name: '2',
                type: 'pie',
                clockWise: false,
                hoverAnimation:false,
                radius: ["45%", "60%"],
                itemStyle: dataStyle
            },
            {
                name: '3',
                type: 'pie',
                clockWise: false,
                hoverAnimation:false,
                radius: ["30%", "45%"],
                itemStyle: dataStyle
            }
        ]
    };


    var main = function (/* param */ p) {

        // 初始化
        _render_init_(p);

        // 执行渲染
        _render_set_(p);

    };

    var _render_init_ = function (p) {


        var e = element = document.getElementById(p.id);

        e.style.width = p.width + 'px';
        e.style.height = p.height + 'px';

    };

    var _render_set_ = function (p) {

        var myChart = echarts.init(element);
        option.series[0].data = [];
        option.series[0].data.push({
            value: p.data[0][0],
            name: '15岁-35岁'
        });
        option.series[0].data.push({
            value: p.data[0][1],
            name: 'invisible',
            itemStyle: placeHolderStyle
        });
        option.series[1].data = [];
        option.series[1].data.push({
            value: p.data[1][0],
            name: '35岁以上'
        });
        option.series[1].data.push({
            value: p.data[1][1],
            name: 'invisible',
            itemStyle: placeHolderStyle
        });
        option.series[2].data = [];
        option.series[2].data.push({
            value: p.data[2][0],
            name: '15岁以下'
        });
        option.series[2].data.push({
            value: p.data[2][1],
            name: 'invisible',
            itemStyle: placeHolderStyle
        });
        // 使用刚指定的配置项和数据显示图表。
        myChart.setOption(option);

    };

    // 接口
    if (!window.render) {

        window.render = function (v) {

            window.render._index_(v);

        };

    }

    window.render._index_ = window.render['de2e185e-4c7e-4a59-88d7-b4a95a063195'] = main;

}());
