$(document).ready(function () {
	$(".horizontalNav li").bind("click", function () {
		var index = $(this).index();
		var divs = $(".mains > div");
		$(this).addClass('activeH').siblings().removeClass('activeH'); 
		divs.hide();//隐藏所有选中项内容
		divs.eq(index).show(); //显示选中项对应内容
	});
});
    
$(".rightMain_choice .radioBg").click(function(){
	$(this).addClass('activeBg').parent().siblings().children().removeClass('activeBg');
});

$(".rightMain_table tbody tr").click(function(){
	$(this).addClass('activeTD').siblings().removeClass('activeTD');
});
