exports.center_btm = function(id, data, categories) {
    var myChart = echarts.init(document.getElementById(id));
    var pdata = [],
        sum0 = 0,
        sum1 = 0,
        sum2 = 0,
        sum3 = 0,
        sum4 = 0,
        sum5 = 0;
    for (var k = 0; k < data[0].length; k++) {
        for (var l = 0; l < data[0][k].length; l++) {
            switch (k) {
                case 0:
                    sum0 += data[0][k][l];
                    break;
                case 1:
                    sum1 += data[0][k][l];
                    break;
                case 2:
                    sum2 += data[0][k][l];
                    break;
                case 3:
                    sum3 += data[0][k][l];
                    break;
                case 4:
                    sum4 += data[0][k][l];
                    break;
                case 5:
                    sum5 += data[0][k][l];
                    break;
            }
        }
    }
    pdata.push({
        value: sum0,
        name: "服装鞋包"
    });
    pdata.push({
        value: sum1,
        name: "母婴用品"
    });
    pdata.push({
        value: sum2,
        name: "家居用品"
    });
    pdata.push({
        value: sum3,
        name: "数码"
    });
    pdata.push({
        value: sum4,
        name: "运动户外"
    });
    pdata.push({
        value: sum5,
        name: "保健"
    });
    option = {
        tooltip: {
            show: true,
            showContent: true,
            backgroundColor: "rgba(50,50,50,0)",
            position: function(point, params, dom) {
                return [point[0] - 15, '12%'];
            },
            formatter: function(params, ticket, callback) {
                if (params.componentSubType == "bar") {
                    return params.data;
                }
            }
        },
        grid:{
            bottom:30
        },
        legend: {
            top: '20',
            right: '10',
            orient: "vertical",
            data: [{
                name: "服装鞋包",
                icon: 'rect',
                textStyle: {
                    color: "#FFF",
                    fontSize: "12",
                    lineHeight: "12",
                    fontFamily: "微软雅黑"
                }
            }, {
                name: "母婴用品",
                icon: 'rect',
                textStyle: {
                    color: "#FFF",
                    fontSize: "12",
                    lineHeight: 12,
                    fontFamily: "微软雅黑"
                }
            }, {
                name: "家居用品",
                icon: 'rect',
                textStyle: {
                    color: "#FFF",
                    fontSize: "12",
                    lineHeight: 12,
                    fontFamily: "微软雅黑"
                }
            }, {
                name: "数码",
                icon: 'rect',
                textStyle: {
                    color: "#FFF",
                    fontSize: "12",
                    lineHeight: 12,
                    fontFamily: "微软雅黑"
                }
            }, {
                name: "运动户外",
                icon: 'rect',
                textStyle: {
                    color: "#FFF",
                    fontSize: "12",
                    lineHeight: 12,
                    fontFamily: "微软雅黑"
                }
            }, {
                name: "保健",
                icon: 'rect',
                textStyle: {
                    color: "#FFF",
                    fontSize: "12",
                    lineHeight: 12,
                    fontFamily: "微软雅黑"
                }
            }],
            itemWidth: 7,
            itemHeight: 7,
            itemGap: 7
        },
        color: ["#2bfeb5", "#3a7df4", "#784ff2", "#f54fc5"],
        xAxis: [{
            type: 'category',
            data: categories,
            splitLine: {
                show: false
            },
            axisLabel: {
                interval: 0,
                textStyle: {
                    color: '#fff',
                    fontSize: '12',
                    fontFamily: "微软雅黑"
                }
            }
        }],
        yAxis: [{
            type: 'value',
            name: '',
            nameTextStyle: {
                color: "#8b9cd2",
                fontSize: '12',
                fontFamily: "微软雅黑"
            },
            min: 0,
            max: 8000,
            interval: 4000,
            axisLabel: {
                textStyle: {
                    color: '#8b9cd2',
                    fontSize: '14',
                    fontFamily: "微软雅黑"
                },
                formatter: '{value}'
            },
            axisLine: {
                show: false
            },
            position: "left",
            splitLine: {
                lineStyle: {
                    color: '#455288',
                    type: 'dashed',
                    width: '1'
                }
            }
        }],
        series: [{
            name: '服装鞋包',
            type: 'bar',
            data: data[0][0],
            barCategoryGap: "50%"
        }, {
            name: '母婴用品',
            type: 'bar',
            data: data[0][1]
        }, {
            name: '家居用品',
            type: 'bar',
            data: data[0][2]
        }, {
            name: '数码',
            type: 'bar',
            data: data[0][3]
        }, {
            name: '运动户外',
            type: 'bar',
            data: data[0][4]
        }, {
            name: '保健',
            type: 'bar',
            data: data[0][5]
        }]
    };
    myChart.setOption(option);
    var j = 1;
    var bb=setInterval(function() {
        var dater = [];
        for (var i = 0; i < 4; i++) {
            dater.push({
                data: data[j][i]
            });
        }
        j++;
        if (j > data.length - 1) j = 0;
        myChart.setOption({
            series: dater
        });
    }, 2000);
}