//上一个下一个按钮

	
	var now = 0;

	$(".nextPrevBtn").delegate('.nextBtn','click',function(){
        var length=$('.zoomin .elecPlatBox').length;
		now++;
		if(now >= length){
			now = 0
		}
		$(".elecPlatBox").eq(now).addClass("active").siblings().removeClass("active");
		exports.clicki(now);
	});
	$(".nextPrevBtn").delegate('.prevBtn','click',function(){
        var length=$('.zoomin .elecPlatBox').length;
		now--;
		if(now <0){
			now = length-1
		}
		$(".elecPlatBox").eq(now).addClass("active").siblings().removeClass("active");
		exports.clicki(now);
	});
	
exports.clicki = function(i){
	if(i ==2){
		exports.zhuBigChart("zhuBigChart");
	}else if(i==1){
		exports.scatterChart("scatterChart")
	}else if(i==3){
		exports.rosePieBigChart("rosePieBigChart")
	}else if(i==0){
		exports.zhuHorizotal("zhuHorizotal")
	}
}

//散点图
exports.scatterChart = function(id) {
   $("#scatterChart").show();
    var width = $("#" + id).width();
    var height = $("#" + id).parent().height();

        var dataSet = [{
                name: "服装",
                color: "rgba(59, 126, 244, 0.8)"
            }, {
                name: "电子",
                color: "rgba(43, 254, 181, 0.8)"
            }, {
                name: "家电",
                color: "rgba(120, 79, 242, 0.8)"
            }, {
                name: "化妆品",
                color: "rgba(245, 79, 197, 0.8)"
            }, {
                name: "食品",
                color: "rgba(253, 255, 168, 0.8)"
            }];

            function createRandomData(dataSet) {
                for(var i = 0; i < dataSet.length; i++) {
                    dataSet[i].data = [];
                    var numItems = Math.round(5 + Math.random() * 20);
                    for(var j = 0; j < numItems; j++) {
                        dataSet[i].data.push({
                            x: Math.round(100000 + Math.round(Math.random() * 20000)),
                            y: Math.round(5 + Math.random() * 99990)
                        });
                    }
                }
                return dataSet;
            }
            
             dataSet = createRandomData(dataSet);

            //测试
          require("./scatter.js").createSatterChart("scatterChart", width, height, dataSet);
          var changeFn=function(y) {
	            //滑块所指定的月份的数据取得，之后更新图表（这里用了随机数据代替后端的ajax请求数据）
//	            console.log(y);
	            require("./scatter.js").createSatterChart("scatterChart", width, height, createRandomData(dataSet));
	        }
          $("#date-slider2").html("");
        require('./verticalSlider.js').setSlider(18, 320, 2016, "date-slider2", changeFn)
    
}
//zhuBigChart 大柱图和折现图
exports.zhuBigChart = function(id) {
	$("#id"+id).show();
    require("./zhuBig.js");
    var width = $("#" + id).width();
    var height = $("#" + id).parent().height() - 30;
    render['8d998cf5-8cc4-4ac9-a2d9-d8fe7051ed40']({
        id: id,
        width: width,
        height: height,
        data : [
			[800, 1500, 2380, 2800, 1880, 1600, 2100, 1100, 1000],
			[2200, 1500, 620, 200, 1120, 1400, 900, 1900, 2000],
			[300, 400, 800, 900, 1000, 1100, 900, 800, 900]
		],
		categories: ['2016年1月', '2016年2月', '2016年3月', '2016年4月', '2016年5月', '2016年6月', '2016年7月', '2016年8月', '2016年9月']
    });
}
