(function () {

    var element;
    // 指定图表的配置项和数据
    option = {
        tooltip: {
            trigger: 'axis'
        },
        legend: {
            show: true,
            textStyle: {
                color: "#fff",
                fontSize: 14,
                fontFamily:"微软雅黑"
            },
            left:20,
            top:15,
            itemGap: 10,
            data: [{
                name: 'T恤',
                icon: 'rect'
            },
                {
                    name: '休闲裤',
                    icon: 'rect'
                }
            ]
        },
        color:["#4adbfd","#6d6afb"],
        xAxis: {
            type: 'category',
            boundaryGap: false,
            axisLabel: {
                textStyle: {
                    fontSize: 12,
                    color: "#8b9dd2"
                },
                margin: 10
            },
            axisTick: {
                length: 8,
                lineStyle: {
                    color: "#58699b",
                    width: 2
                }
            },
            splitLine: {
                lineStyle: {
                    color: "#232364",
                    opacity: 0.8
                }
            },
            axisLine: {
                lineStyle: {
                    color: "#58699b"
                }
            }
        },
        yAxis: {
            type: 'value',
            min: 0,
            max: 7000,
            interval: 1000,
            splitNumber: 5,
            axisLabel: {
                margin: 10,
                textStyle: {
                    fontSize: 12,
                    color: "#8b9cd2",
                    fontFamily: "微软雅黑"
                }
            },
            axisTick: {
                show: false
            },
            axisLine: {
                show: true
            },
            splitLine: {
                lineStyle: {
                    color: '#7887BB', //分割线颜色
                    type: "dashed",
                    opacity: 0.5
                }
            }
        },
        series: [
            {
                name: 'T恤',
                type: 'line',
                smooth: true,
                symbol: '../../img/1.png',
                symbolSize: 15,
                lineStyle: {
                    normal: {
                        color: '#5551CF'        //线的颜色
                    }
                },
                markLine: {
                    symbol: "none",
                    lineStyle: {
                        normal: {
                            color: "#6d6afb"
                        }
                    },
                    data: [
                        {
                            type: 'average', name: '平均值',
                            lineStyle: {
                                normal: {
                                    width: 2
                                }
                            }, label: {
                            normal: {
                                show: false
                            }
                        }
                        }
                    ]
                }
            },
            {
                name: '休闲裤',
                type: 'line',
                smooth: true,
                symbol: '../../img/1.png',
                symbolSize: 15,
                lineStyle: {
                    normal: {
                        color: '#43cdcf'        //线的颜色
                    }
                },
                markLine: {
                    symbol: "none",
                    lineStyle: {
                        normal: {
                            color: "#4adbfd"
                        }
                    },
                    data: [
                        {
                            type: 'average', name: '平均值',
                            lineStyle: {
                                normal: {
                                    width: 2
                                }
                            }, label: {
                            normal: {
                                show: false
                            }
                        }
                        }
                    ]
                }
            },
        ]
    };

    var main = function (/* param */ p) {

        // 初始化
        _render_init_(p);

        // 执行渲染
        _render_set_(p);

    };

    var _render_init_ = function (p) {
        // 指定图表的配置项和数据

        var e = element = document.getElementById(p.id);

        e.style.width = p.width + 'px';
        e.style.height = p.height + 'px';

    };

    var _render_set_ = function (p) {

        var myChart = echarts.init(element);

        option.series[0].data = p.data[0];
        option.series[1].data = p.data[1];
        option.xAxis.data = p.categories;

        // 使用刚指定的配置项和数据显示图表。
        myChart.setOption(option);

    };

    // 接口
    if (!window.render) {

        window.render = function (v) {

            window.render._index_(v);

        };

    }

    window.render._index_ = window.render['fa9e8f04-ea91-45be-b4de-b1e0f8aceab7'] = main;

}());
