(function () {

    var element;
    // 指定图表的配置项和数据
    var option = {
        legend: {
            data: [{name: '销售额（万元）', icon: "rect"}, {name: '增长率', icon: "circle"}],
            top: "10",
            left: "10",
            textStyle: {
                color: "#fff",
                fontSize: 14,
                fontFamily: '微软雅黑'
            },
            itemHeight: 14,
            itemWidth: 14,
            itemGap: 30
        },
        tooltip: {
            trigger: "axis",
            formatter: "{b0}<br/>销售额 : {c0}<br />增长率 : {c2}%",
            padding:10,
            borderWidth:1,
            borderColor:"#595180",
            textStyle:{
                fontSize:14,
                fontFamily:"微软雅黑"
            },
            backgroundColor:"rgba(19,12,79,0.8)",
            axisPointer: {
                type: "line",
                lineStyle:{
                    color:"#8da8b4"
                }
            }
        },
        grid: {
            left:60
        },
        xAxis: [
            {
                type: 'category',
                axisLabel: {
                    textStyle: {
                        color: "#8b9dd2",
                        fontSize: 12,
                        fontFamily: '微软雅黑'
                    },
                    margin: 10,
                    interval: 0
                },
                axisTick: {
                    length: 8,
                    lineStyle: {
                        color: "#404E79",
                        width: 2
                    }
                },
                splitLine: {
                    show: false
                },
                axisLine: {
                    lineStyle: {
                        color: "#404E79"
                    }
                }
            }
        ],
        yAxis: [
            {
                type: 'value',
                min: 0,
                max: 6000,
                interval: 1000,
                axisLabel: {
                    formatter: function (v) {
                        return v + '\n'
                    },
                    margin: 10,
                    textStyle: {
                        fontSize: 12,
                        fontFamily: '微软雅黑',
                        color: "#8b9dd2"
                    }
                },
                axisTick: {
                    show: false
                },
                axisLine: {
                    show: false
                },
                splitLine: {
                    lineStyle: {
                        color: '#4d5e91', //分割线颜色
                        type: "dashed"
                    }
                }
            },
            {
                type: 'value',
                min: 0,
                max: 60,
                interval: 10,
                axisLabel: {
                    formatter: function (v) {
                        return v + '%\n'
                    },
                    textStyle: {
                        color: "#9c9aac",
                        fontSize: 12,
                        fontFamily: '微软雅黑'
                    }
                },
                axisTick: {
                    show: false
                },
                axisLine: {
                    show: false
                },
                splitLine: {
                    lineStyle: {
                        color: '#4d5e91', //分割线颜色
                        type: "dashed"
                    }
                }
            }
        ],
        color: ["#3b7ef4", "#3dfdb7", "#3dfdb7"],
        series: [
            {
                name: '销售额（万元）',
                type: 'bar',
                stack: '总量1',
                itemStyle: {
                    normal: {
                        color: "#3BA2F9"
                    }
                },
                barCategoryGap: "60%"
            },
            {
                name: '销售额（万元）',
                type: 'bar',
                stack: '总量1',
                itemStyle: {
                    normal: {
                        color: "#fff",
                        opacity: 0.1
                    }
                },
                barCategoryGap: "40%"
            },

            {
                name: '增长率',
                type: 'line',
                yAxisIndex: 1,
                smooth: true,
                symbol: 'circle',
                symbolSize: 10,
                itemStyle: {
                    normal: {
                        opacity:0
                    },
                    emphasis:{
                        opacity:1
                    }
                },
                lineStyle: {
                    normal: {
                        width: 3
                    }
                }
            }
        ]
    };

    var main = function (/* param */ p) {

        // 初始化
        _render_init_(p);

        // 执行渲染
        _render_set_(p);

    };

    var _render_init_ = function (p) {
        // 指定图表的配置项和数据

        var e = element = document.getElementById(p.id);

        e.style.width = p.width + 'px';
        e.style.height = p.height + 'px';

    };

    var _render_set_ = function (p) {

        var myChart = echarts.init(element);

        option.series[0].data = p.data[0];
        option.xAxis[0].data = p.categories;
        option.series[1].data = p.data[1];
        option.series[2].data = p.data[2];


        // 使用刚指定的配置项和数据显示图表。
        myChart.setOption(option);

    };

    // 接口
    if (!window.render) {

        window.render = function (v) {

            window.render._index_(v);

        };

    }

    window.render._index_ = window.render['8d998cf5-8cc4-4ac9-a2d9-d8fe7051ed42'] = main;

}());
