exports.createHillClumn = function(id, width, height, dataset) {
    /* tooltip */
    var tipTimerConfig = {
        longer: 0,
        target: null,
        exist: false,
        winEvent: window.event,
        boxHeight: 398,
        boxWidth: 376,
        maxWidth: 376,
        maxHeight: 398,
        tooltip: null,

        showTime: 3500,
        hoverTime: 300,
        displayText: "",
        show: function(val, e) {
            "use strict";
            var me = this;

            if (e != null) {
                me.winEvent = e;
            }

            me.displayText = val;

            me.calculateBoxAndShow();

            me.createTimer();
        },
        calculateBoxAndShow: function() {
            "use strict";
            var me = this;

            //计算文字宽度，调整tooltip
            //      var calculate = document.getElementById('tip-div');
            //      if (calculate == null) {
            //          calculate = document.createElement("div");
            //          calculate.className = 'div-tip-hidden'
            //          calculate.setAttribute('id', 'tip-div');
            //          document.getElementById("menu").appendChild(calculate);
            //      }
            //      calculate.innerHTML = me.getInnerHtml();
            //      var s = calculate.getElementsByTagName('SPAN')[0];
            //      me.boxWidth = s.offsetWidth + 8;
            //      if (me.boxWidth > me.maxWidth) {
            //          me.boxWidth = me.maxWidth;
            //      }
            //      me.boxHeight = s.offsetHeight + 8;

            var _x = 0;
            var _y = 0;
            var _w = document.documentElement.scrollWidth;
            var _h = document.documentElement.scrollHeight;

            var xMouse = me.winEvent.x + window.scrollX;
            if (_w - xMouse < me.boxWidth) {
                _x = xMouse - me.boxWidth - 10;
            } else {
                _x = xMouse;
            }

            var _yMouse = me.winEvent.y + window.scrollY;
            if (_h - _yMouse < me.boxHeight + 18) {
                _y = _yMouse - me.boxHeight - 25;
            } else {

                _y = _yMouse + 18;
            }

            me.addTooltip(_x, _y);
        },
        addTooltip: function(page_x, page_y) {
            "use strict";
            var me = this;

            me.tooltip = document.createElement("div");
            me.tooltip.style.left = page_x + "px";
            me.tooltip.style.top = page_y + "px";
            me.tooltip.style.position = "absolute";

            me.tooltip.style.width = me.boxWidth + "px";
            me.tooltip.style.height = me.boxHeight + "px";
            me.tooltip.className = "three-tooltip";

            var divInnerHeader = me.createInner();
            //      divInnerHeader.innerHTML = me.getInnerHtml(me.boxWidth);
            divInnerHeader.innerHTML = me.displayText;
            me.tooltip.appendChild(divInnerHeader);

            document.body.appendChild(me.tooltip);
        },
        createInner: function() {
            "use strict";
            var me = this;
            var divInnerHeader = document.createElement('div');
            divInnerHeader.style.width = me.boxWidth + "px";
            divInnerHeader.style.height = me.boxHeight + "px";
            return divInnerHeader;
        },
        ClearDiv: function() {
            "use strict";
            var delDiv = document.body.getElementsByClassName("three-tooltip");
            for (var i = delDiv.length - 1; i >= 0; i--) {
                document.body.removeChild(delDiv[i]);
            }
        },
        //  getInnerHtml: function(w) {
        //      "use strict";
        //      var tip = new Array();
        //      if (w == null) {
        //          w = this.maxWidth;
        //      }
        //      tip.push('<table style="font-size:14px;" width="' + w + 'px">');
        //      tip.push("<tr>");
        //      tip.push('<td>');
        //      tip.push('<span class="tooltip-font-div">' + this.displayText + '</span>');
        //      tip.push("</td>");
        //      tip.push("</tr>");
        //      return tip.join('');
        //  },
        createTimer: function(delTarget) {
            "use strict";
            var me = this;
            var delTip = me.tooltip;
            var delTarget = tipTimerConfig.target;
            var removeTimer = window.setTimeout(function() {
                try {
                    if (delTip != null) {
                        document.body.removeChild(delTip);
                        if (tipTimerConfig.target == delTarget) {
                            me.exist = false;
                        }
                    }
                    clearTimeout(removeTimer);
                } catch (e) {
                    clearTimeout(removeTimer);
                }
            }, me.showTime);
        },
        hoverTimerFn: function(showTip, showTarget) {
            "use strict";
            var me = this;

            var showTarget = tipTimerConfig.target;

            var hoverTimer = window.setInterval(function() {
                try {
                    if (tipTimerConfig.target != showTarget) {
                        clearInterval(hoverTimer);
                    } else if (!tipTimerConfig.exist && (new Date()).getTime() - me.longer > me.hoverTime) {
                        //show
                        tipTimerConfig.show(showTip);
                        tipTimerConfig.exist = true;
                        clearInterval(hoverTimer);
                    }
                } catch (e) {
                    clearInterval(hoverTimer);
                }
            }, tipTimerConfig.hoverTime);
        }
    };

    //marker.mouseOut = function() {
    //  tipTimerConfig.target = null;
    //  tipTimerConfig.ClearDiv();
    //};
    //marker.mouseOver = function() {
    //  // 添加 div
    //  tipTimerConfig.target = this;
    //  tipTimerConfig.longer = new Date().getTime();
    //  tipTimerConfig.exist = false;
    //  //获取坐标
    //  tipTimerConfig.winEvent = {
    //      x: 0,
    //      y: 0
    //  };
    //
    //  tipTimerConfig.boxHeight = 398;
    //  tipTimerConfig.boxWidth = 376;
    //
    //  //hide
    //  tipTimerConfig.ClearDiv();
    //  tipTimerConfig.hoverTimerFn(createTooltipTableData(this.info));
    //};
    var publicGraph = {};
    var color = [
        "#2bfeb5",
        "#3b7ef4",
        "#784ff2",
        "#f54fc5",
        "#f0a629",
        "#fafd7c",
        "#334F9C",
        "#216AB2",
        "#358FCD",
        "#30B1CA",
        "#19A987",
        "#298F49",
        "#91BF3F"
    ];
    //------------------------------function begin--------------------------
    var createTooltipTableData = function(info) {
            var ary = [];

            ary.push("<div class = 'tip-hillClumn' >");
            ary.push("<table class ='table-tip-list'>");
            ary.push("<tr>");
            ary.push("<td>");
            ary.push(info.name);
            ary.push(" : ");
            ary.push("</td>");
            ary.push("<td>");
            ary.push(info.val);
            ary.push("</td>");
            ary.push("<tr>");
            ary.push("</table>");
            ary.push("</div>");

            return ary.join("");
        }
        //------------------------------function end--------------------------

    //画布周边的空白
    var padding = {
        left: 30,
        right: 30,
        top: 30,
        bottom: 70
    };

    for (var i = 0; i < dataset.length; i++) {
        dataset[i].color = color[i];
    }

    var _width = width - padding.left - padding.right;
    var _height = height - padding.top - padding.bottom;

    //绘制一个svg
    var svg = d3.select("#" + id).append("svg")
        .attr("width", width)
        .attr("height", height);

    //x轴的比例尺
    var xScale = d3.scale.ordinal()
        .domain(dataset.map(function(d) {
            return d.name
        }))
        .rangeRoundBands([0, _width]);

    //y轴的比例尺
    var yScale = d3.scale.linear()
        .domain([0, d3.max(dataset, function(d) {
            return d.val;
        }) * 1.2])
        .range([height - padding.top - padding.bottom, 0]);

    var xAxis = d3.svg.axis()
        .scale(xScale)
        .orient("bottom")
        .ticks(dataset.length);

    //矩形之间的空白
    var rectPadding = -35;

    var getPath = function(d) {

        var _y = (_height - yScale(d.val));
        var _x_offset = radios * 0.2;
        var _w = radios - rectPadding;

        var ary = [];
        ary.push('M0,' + _y);
        ary.push(' ');
        ary.push('C' + _x_offset);
        ary.push(' ');
        ary.push(_y);
        ary.push(' ');
        ary.push(_w / 2 - _x_offset * 2);
        ary.push(' ');
        ary.push(0);
        ary.push(' ');
        ary.push(_w / 2);
        ary.push(' ');
        ary.push(0);
        ary.push(' ');

        ary.push('C' + (_w / 2 + _x_offset * 2));
        ary.push(' ');
        ary.push(0);
        ary.push(' ');
        ary.push(_w - _x_offset);
        ary.push(' ');
        ary.push(_y);
        ary.push(' ');
        ary.push(_w);
        ary.push(' ');
        ary.push(_y);
        ary.push(' ');

        ary.push("L0,");
        ary.push(_y);
        ary.push(' ');
        ary.push("Z");

        return ary.join('');
    };
    var getPathByBig = function(d) {

        var _y = _height - yScale(d.val) + 5;
        var _x_offset = radios * 0.2;
        var _w = radios - rectPadding + 5;

        var ary = [];
        ary.push('M0,' + _y);
        ary.push(' ');
        ary.push('C' + _x_offset);
        ary.push(' ');
        ary.push(_y);
        ary.push(' ');
        ary.push(_w / 2 - _x_offset * 2);
        ary.push(' ');
        ary.push(0);
        ary.push(' ');
        ary.push(_w / 2);
        ary.push(' ');
        ary.push(0);
        ary.push(' ');

        ary.push('C' + (_w / 2 + _x_offset * 2));
        ary.push(' ');
        ary.push(0);
        ary.push(' ');
        ary.push(_w - _x_offset);
        ary.push(' ');
        ary.push(_y);
        ary.push(' ');
        ary.push(_w);
        ary.push(' ');
        ary.push(_y);
        ary.push(' ');

        ary.push("L0,");
        ary.push(_y);
        ary.push(' ');
        ary.push("Z");

        return ary.join('');
    };
    var gMain = svg.selectAll("svg")
        .data(dataset)
        .enter()
        .append("g")
        .attr("transform", "translate(" + padding.left + "," + padding.top + ")")
        .on("mouseover", function(d) {
            d3.select(this).select(".realPath")
                .attr("d", getPathByBig(d))
                .attr("transform", function(d) {
                    return "translate(" + (xScale(d.name) + rectPadding / 2 - 2.5) + "," + (yScale(d.val) - 5) + ")";
                });
            d3.select(this).select(".transparentPath")
                .attr("opacity", 0.2);
            // 添加 div
            tipTimerConfig.target = this;
            tipTimerConfig.longer = new Date().getTime();
            tipTimerConfig.exist = false;
            //获取坐标
            tipTimerConfig.winEvent = {
                x: event.clientX,
                y: event.clientY
            };

            tipTimerConfig.boxHeight = 50;
            tipTimerConfig.boxWidth = 140;

            //hide
            tipTimerConfig.ClearDiv();
            //show
            tipTimerConfig.hoverTimerFn(createTooltipTableData(d));
        })
        .on("mouseout", function(d) {
            d3.select(this).select(".realPath")
                .attr("d", getPath(d))
                .attr("transform", function(d) {
                    return "translate(" + (xScale(d.name) + rectPadding / 2) + "," + yScale(d.val) + ")";
                });
            d3.select(this).select(".transparentPath")
                .attr("opacity", 0);
            tipTimerConfig.target = null;
            tipTimerConfig.ClearDiv();
        });

    var xAxisG = svg.append("g")
        .call(xAxis)
        .attr("transform", "translate(" + padding.left + "," + (_height + padding.top) + ")");
    xAxisG.select("path").remove();
    xAxisG.selectAll("text")
        .attr("font-size", "14px")
        .attr("font-family", "微软雅黑")
        .attr("fill", "#FFF");
    var radios = _width / dataset.length;

    gMain.append("path")
        .attr("class", "realPath")
        .attr("d", function(d) {

            var _y = (_height - yScale(d.val));
            var _x_offset = radios * 0.2;
            var _w = radios - rectPadding;

            var ary = [];
            ary.push('M0,' + _y);
            ary.push(' ');
            ary.push('C' + _x_offset);
            ary.push(' ');
            ary.push(_y);
            ary.push(' ');
            ary.push(_w / 2 - _x_offset * 2);
            ary.push(' ');
            ary.push(_y);
            ary.push(' ');
            ary.push(_w / 2);
            ary.push(' ');
            ary.push(_y);
            ary.push(' ');

            ary.push('C' + (_w / 2 + _x_offset * 2));
            ary.push(' ');
            ary.push(_y);
            ary.push(' ');
            ary.push(_w - _x_offset);
            ary.push(' ');
            ary.push(_y);
            ary.push(' ');
            ary.push(_w);
            ary.push(' ');
            ary.push(_y);
            ary.push(' ');

            ary.push("L0,");
            ary.push(_y);
            ary.push(' ');
            ary.push("Z");

            return ary.join('');
        })
        .attr("transform", function(d, i) {
            return "translate(" + (xScale(d.name) + rectPadding / 2) + "," + yScale(d.val) + ")";
        })
        .attr("fill", function(d, i) {
            return d.color;
        })
        .transition()
        .duration(1000)
        .delay(function(d, i) {
            return i * 100;
        })
        .attr("d", function(d) {
            return getPath(d);
        });
    gMain.append("path")
        .attr("class", "transparentPath")
        .attr("opacity", 0)
        .attr("transform", function(d, i) {
            return "translate(" + (xScale(d.name) + rectPadding / 2 - 2.5) + "," + (yScale(d.val) - 5) + ")";
        })
        .attr("fill", "#FFF")
        .attr("d", function(d) {
            return getPathByBig(d);
        });
    gMain.attr("opacity", 0)
        .transition()
        .duration(1000)
        .delay(function(d, i) {
            return i * 100;
        })
        .attr("opacity", 1);
    return publicGraph;
}