exports.createSatterChart=function(id, width, height, dataset) {
    var publicGraph = {};

    //------------------------------function begin--------------------------

    //------------------------------function end--------------------------
    var baseImg = "data:image/png;base64,";
    var maxR = 60;
    var minR = 10;
    var _max = 0;
    for(var i = 0; i < dataset.length; i++) {

        for(var j = 0; j < dataset.length; j++) {
            if(_max < dataset[i].data[j].y) {
                _max = dataset[i].data[j].y;
            }
        }
    }
    var yScale = function(val) {
        var ret = val * (maxR / _max);
        if(ret < minR) ret = minR;
        return ret;
    }

    var legendList = [];
    for(var i = 0; i < dataset.length; i++) {
        legendList.push({
            icon: "circle",
            name: dataset[i].name
        });
    }

    var series = [];
    for(var i = 0; i < dataset.length; i++) {
        var _d = [];
        for(var j = 0; j < dataset.length; j++) {
            _d.push([dataset[i].data[j].x,
                dataset[i].data[j].y,
                yScale(dataset[i].data[j].y)
            ]);
        }
        series.push({
            name: dataset[i].name,
            data: _d,
            type: 'scatter',
            symbolSize: function(data) {
                return data[2];
            },
            itemStyle: {
                normal: {
                    color: dataset[i].color
                }
            }
        });
    }

    var myChart = echarts.init(document.getElementById(id));
    var option = {
        tooltip: {
            trigger: 'item',
            padding: 15,
            borderWidth: 1,
            borderColor: "#239a8c",
            backgroundColor: "rgba(19,12,79,0.8)",
            formatter: function(params, ticket, callback) {
                var tip = [];

                tip.push("<img src='");
                tip.push(baseImg);
                tip.push("' style='border:0px;padding:0px;display:inline-block;background: ");
                tip.push(params.color);
                tip.push(";border-radius: 50%;width: 11px; height:11px;' />");
                tip.push("  ");
                tip.push(params.seriesName);
                tip.push("<Br/>");
                tip.push("销售额：");
                tip.push(params.data[1]);
                tip.push("万元");
                tip.push("<Br/>");                
                tip.push("店铺数：");
                tip.push(params.data[0]);
                tip.push("家");

                return tip.join('');
            },
            textStyle: {
                color: "#FFFFFF",
                fontFamily: "微软雅黑",
                fontSize: 14
            }
        },

        grid: {
            right: "20%"
        },
        legend: {
            left: 25,
            itemWidth : 11,
            itemHeight : 11,
            textStyle: {
                color: "#FFFFFF",
                fontFamily: "微软雅黑",
                fontSize: 14
            },
            padding: 5,
            itemGap: 25,
            data: legendList
        },
        xAxis: {
            name: "销售额（万元）",
            nameTextStyle: {
                color: "#FFFFFF",
                fontFamily: "微软雅黑",
                fontSize: 14
            },
            splitLine: {
                show: false
            },
            nameGap: 20,
            type: "value",
            axisLabel: {
                textStyle: {
                    color: "#8b9dd2",
                    fontFamily: "微软雅黑",
                    fontSize: 14
                }
            },
            scale: true,
            axisLine: {
                lineStyle: {
                    color: "#8b9dd2"
                }
            },
            axisTick: {
                lineStyle: {
                    color: "#8b9dd2"
                }
            }
        },
        yAxis: {
            name: "店铺数（家）",
            nameTextStyle: {
                color: "#FFFFFF",
                fontFamily: "微软雅黑",
                fontSize: 14
            },
            splitLine: {
                show: false
            },
            type: "value",
            scale: true,
            axisLabel: {
                textStyle: {
                    color: "#8b9dd2",
                    fontFamily: "微软雅黑",
                    fontSize: 14
                }
            },
            axisLine: {
                lineStyle: {
                    color: "#8b9dd2"
                }
            },
            axisTick: {
                lineStyle: {
                    color: "#8b9dd2"
                }
            }
        },
        series: series
    };
    myChart.setOption(option);
    return publicGraph;
}