//Tab切换
exports.init=function(){
$(".Tab").delegate("li", "click", function() {
    var index = $(this).index();
    $(this).addClass("active").siblings().removeClass("active");
    $(".storesInfoBox .TabCon").eq(index).addClass("active").siblings().removeClass("active");
    if (index == 1) {
    	$("#shopsaleInfoL").html("")
    	$("#shopsaleInfoR").html("")
        exports.shopsaleInfoL('shopsaleInfoL');
        exports.shopsaleInfoR('shopsaleInfoR');
    } else if (index == 2) {
    	$("#shopOperativeInfoL").html("")
    	$("#shopOperativeInfoC").html("")
    	$("#shopOperativeInfoR").html("")
        exports.shopOperativeInfoL('shopOperativeInfoL');
        exports.shopOperativeInfoC('shopOperativeInfoC');
        exports.shopOperativeInfoR('shopOperativeInfoR');
    }


})
$(".competitorBox").delegate('.divselectDd', 'click', function() {
        $(".divSelectDrop").toggleClass("active");
    })
}
