exports.setCenter = function() {
    var set_chart = require('./component/set_chart.js')
    $("#small_left_top").click(function() {
        $('.showBox').addClass('zoomout')
        $('.showBox .inner').html("")
        var obj = $(".big_left_top_wrap").clone(true);
        obj.appendTo($('.showBox .inner'));
        set_chart.bir_left_top('big_left_top');
        $('.showBox').addClass('zoomin').removeClass('zoomout');
    })

    $("#small_left_center").click(function() {
        $('.showBox').addClass('zoomout')
        $('.showBox .inner').html("")
        var obj = $(".big_left_center_wrap").clone(true);
        obj.appendTo($('.showBox .inner'));
        set_chart.big_left_center('big_left_center');
        $('.showBox').addClass('zoomin').removeClass('zoomout');
    })
    $("#small_right_top").click(function() {
        $('.showBox').addClass('zoomout')
        $('.showBox .inner').html("")
        var obj = $(".big_right_top_wrap").clone(true);
        obj.appendTo($('.showBox .inner'));
        set_chart.big_right_top('big_right_top');
        $('.showBox').addClass('zoomin').removeClass('zoomout');
    })
    $("#small_right_center").click(function() {
        $('.showBox').addClass('zoomout')
        $('.showBox .inner').html("")
        var obj = $(".big_right_center_wrap").clone(true);
        obj.appendTo($('.showBox .inner'));
        set_chart.big_right_center('big_right_center');
        $('.showBox').addClass('zoomin').removeClass('zoomout');
    })
    $("#small_right_btm").click(function() {
        $('.showBox').addClass('zoomout')
        $(".ppbox").hide();
        $('.showBox .inner').html("")
        var obj = $(".big_right_bottom_wrap").clone(true);
        obj.appendTo($('.showBox .inner'));
        set_chart.right_bottom_big('big_right_bottom');
        $('.showBox').addClass('zoomin').removeClass('zoomout');
    })
    $("#small_center_btm").click(function() {
        $('.showBox').addClass('zoomout')
        $(".ppbox").hide();
        $('.showBox .inner').html("")
        var obj = $(".big_center_bottom_wrap").clone(true);
        obj.appendTo($('.showBox .inner'));
        set_chart.big_center_bottom('big_center_bottom');
        $('.showBox').addClass('zoomin').removeClass('zoomout');
    })
    $("#small_center").click(function() {
 
    })
    $("#small_left_bottom").click(function() {
        $('.showBox').addClass('zoomout')
        $(".ppbox").hide();
        $('.showBox .inner').html("")
        var obj = $(".innerBox").clone(true);
        obj.appendTo($('.showBox .inner'));
        set_chart.right_bottom_big('big_right_bottom');
        $('.showBox').addClass('zoomin').removeClass('zoomout');
    })
    $(".returnCenter").click(function() {
        $('.showBox').removeClass('zoomin').addClass("zoomout")
        setTimeout(function() {
            $('.showBox').removeClass("zoomout")
        }, 1000)
        $('.showBox .inner').html("")
    })
    $(".ppbox .close").click(function() {
        $(".ppbox ").hide();
    })

    $(".khfx").on('click', function() {
        $('.showBox .inner').html("")
        var obj = $(".ObjFeatureBox").clone(true);
        obj.appendTo($('.showBox .inner'));
        require("./component/ywt/index.js").init()
        require("./component/ywt/index.js").init2('TrightCharts')
    })
    $(".innerBox_yw .titleBtn span").eq(0).on('click', function() {

        $('.showBox .inner').html("")
        var obj = $(".ObjFeatureBox").clone(true);
        obj.appendTo($('.showBox .inner'));
        require("./component/ywt/index.js").init()
        require("./component/ywt/index.js").init2('TrightCharts')
    })
    $(".innerBox_yw .titleBtn span").eq(1).on('click', function() {

        $('.showBox .inner').html("")
        var obj = $(".innerBox").clone(true);
        obj.appendTo($('.showBox .inner'));
    })
    $(".ObjFeatureBox .ObjFeature_ul li:first").on('click', function() {

        $('.showBox .inner').html("")
        var obj = $(".innerBox").clone(true);
        obj.appendTo($('.showBox .inner'));
    })
    $(".ObjFeatureBox .rightMain_table tr td").eq(1).on('click', function() {
        $('.showBox .inner').html("")
        var obj = $(".innerBox_yw").clone(true);
        obj.appendTo($('.showBox .inner'));
    })

    $("#loginBtn").click(function() {
        var v1 = $(".accountNumber").val();
        var v2 = $(".passWord").val();
        if (v1 == "jinhua" && v2 == '123456') {


            $(".loginContentBox").addClass("hinge")
            $(".loginContentBox").hide()
        } else {
            alert('用户名或密码错误!')
        }
    })
}
exports.setClick = function(cid, mid, oid) {
    $("#" + cid).click(function() {
        $('.showBox .inner').html("")
        var obj = $("." + mid).clone(true);
        obj.appendTo($('.showBox .inner'));
        set_chart.bir_left_top(oid);
        $('.showBox').addClass('zoomin').removeClass('zoomout');
    })

}
