exports.center_bottom = function(id) {
    $("#" + id).css({
        width: $("#" + id).parent().width(),
        height: $("#" + id).parent().height()
    })
    var c = require("./small_center_btm.js");
    var section = 6 //地区
    var year = 7; //2014-2017
    var categories = 9; //类目
    var data = [];
    for (var l = 0; l < section; l++) {
        var a = [];
        for (var i = 0; i < year; i++) {
            var b = [];
            for (var j = 0; j < categories; j++) {
                b.push(3000 + Math.floor(Math.random() * 2000));
            }
            a.push(b);
        }
        data.push(a);
    }
    c.center_btm(id, data, ['义乌', '永康', '金东', '兰溪', '婺城', '东阳', '浦江', '武义', '磐安']);
}
