exports.big_center_bottom = function(id) {
    var s = require("./biig_center_btm.js");
    var width = $("#" + id).width();
    var height = $("#" + id).parent().height();
    $("#" + id).css({
        width: width,
        height: height
    })
    var year = 3; //2014-2016
    var section = 6 //产业
    var categories = 9; //类目
    var data = [];
    for (var l = 0; l < year; l++) {
        var a = [];
        for (var i = 0; i < section; i++) {
            var b = [];
            for (var j = 0; j < categories; j++) {
                if (i == 0) {
                    b.push((9 - j) * 500 + Math.floor(Math.random() * 3500));
                } else {
                    b.push((9 - j) * 500 + Math.floor(Math.random() * 1000));
                }

            }
            a.push(b);
        }
        data.push(a);
    }
    //可以在控制台查看数据格式
    s.setBarCharts(id, data, ['义乌', '永康', '金东', '兰溪', '婺城', '东阳', '浦江', '武义', '磐安']);
}
