(function () {

    var element;
    // 指定图表的配置项和数据
    var option = {
        grid:{
          left:60,
          top:45,
          bottom:15,
          right:20
        },
        legend: {
            top:20,
            itemWidth: 12,
            itemHeight: 12,
            itemGap: 30,
            data: [
                {
                    name: "税务登记实体数",
                    icon: "rect",
                    textStyle: {
                        color: "#fff",
                        fontSize: 12,
                        fontFamily: "微软雅黑"
                    }
                },
                {
                    name: "实际电商实体数",
                    icon: "rect",
                    textStyle: {
                        color: "#fff",
                        fontSize: 12,
                        fontFamily: "微软雅黑"
                    }
                }
            ]

        },
        yAxis: [
            {
                type: 'category',
                minInterval: 10,
                axisTick: {
                    length: 0
                },
                axisLabel: {
                    interval: 0,
                    margin: 10,
                    textStyle: {
                        color: "#fff",
                        fontSize: 12,
                        fontFamily: "微软雅黑"
                    },
                    formatter: function (v) {
                        return v;
                    }
                },
                splitLine: {
                    show: false
                },
                axisLine: {
                    show: false,
                    lineStyle: {
                        color: "#404E79"
                    }
                }
            }
        ],

        xAxis: [
            {
                type: 'value',
                nameTextStyle: {
                    color: '#fff'
                },
                nameGap: 25,
                axisLabel: {
                    margin: 0,
                    textStyle: {
                        fontSize: 12,
                        color: "#8b9dd2"
                    }

                },
                minInterval: 100,
                axisTick: {
                    show: true,
                    length: 5,
                    inside: true,
                    lineStyle: {
                        color: "#58699b"
                    }
                },
                axisLine: {
                    show: true,
                    lineStyle: {
                        color: "#58699b"
                    }
                },
                splitLine: {
                    show: false
                }
            }
        ],
        series: [
            {
                name: '税务登记实体数',
                type: 'bar',
                itemStyle: {
                    normal: {
                        color: "#2bfeb5"
                    }
                },
                barCategoryGap: "40%",
                barGap:"50%"
            },
            {
                name: '实际电商实体数',
                type: 'bar',
                itemStyle: {
                    normal: {
                        color: "#3b7ef4"
                    }
                }
            }
        ]
    };

    var main = function (/* param */ p) {

        // 初始化
        _render_init_(p);

        // 执行渲染
        _render_set_(p);

    };

    var _render_init_ = function (p) {
        // 指定图表的配置项和数据

        var e = element = document.getElementById(p.id);

        e.style.width = p.width + 'px';
        e.style.height = p.height + 'px';

    };

    var _render_set_ = function (p) {

        var myChart = echarts.init(element);

        option.series[0].data = p.data[0];
        option.series[1].data = p.data[1];
        option.yAxis[0].data = p.categories;

        // 使用刚指定的配置项和数据显示图表。
        myChart.setOption(option);

    };

    // 接口
    if (!window.render) {

        window.render = function (v) {

            window.render._index_(v);

        };

    }

    window.render._index_ = window.render['61e2960f-560c-4039-97b0-6e1c5e73cff5'] = main;

}());
